.header-main{
    width: 100%;
}
.header-appbar{
    width: 100%;
    height: 9vh;
    background: linear-gradient(to right top, #000000, #181818, #282828, #3a3a3a, #4c4c4c);
}
.header-main Button{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 8px 15px;
    border-radius: 12px;
    margin: 0 5px;
    transition: all 0.3s;
}
.header-main Button:hover{
    color: black;
    font-weight: 600;
    background-color: #797979;
}
